<template>
<div>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i>订单管理</el-breadcrumb-item>
        <el-breadcrumb-item>创建订单</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container>
      <el-header style="align-items: center;margin-top:20px;">
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>分配参数表</span>
            </div>
            <div>
              <div style="min-height:15px;width:100%;">
                <el-table :data="tableData6" border show-summary style="width: 100%">
                  <el-table-column prop="projectName" label="组长">
                  </el-table-column>
                  <el-table-column prop="piWenDaiMa" label="未完成单数">
                  </el-table-column>
                  <el-table-column prop="addUserName" label="本月接单面积">
                  </el-table-column>
                  <el-table-column prop="createTime" label="总接单面积">
                  </el-table-column>
                  <el-table-column prop="finishTime" label="现有简单率">
                  </el-table-column>
                  <el-table-column prop="gongXu" label="简单率">
                  </el-table-column>
                </el-table>
              </div>
            </div>
          </el-card>
        </div>
        <!-- 选择模 -->
        <div>
          <el-card class="box-card" style="margin-top:25px;">
            <div slot="header" class="clearfix">
              <span>员工工作量统计</span>
              <!-- <div style="float:right">
                    <el-button  type="primary" @click="toChangeSet">修 改</el-button>
                  </div> -->
            </div>
            <div>
              <el-row :gutter="30">
                <el-col :span='4' v-loading="tree_loading">
                  <el-input placeholder="输入关键字进行过滤" v-model="filterText">
                  </el-input>
                  <el-tree class="filter-tree" @check-change="toChangeCheck" :data="data3" :props="defaultProps" default-expand-all :filter-node-method="filterNode" ref="tree2" show-checkbox node-key="id" style="border-right: 1px solid #e6e6e6;min-height:260px;">
                  </el-tree>
                </el-col>
                <el-col :span='20'>
                  <div style="min-height:15px;width:100%;">
                    <el-table :data="tableData6" border show-summary style="width: 100%">
                      <el-table-column prop="projectName" label="项目">
                      </el-table-column>
                      <el-table-column prop="piWenDaiMa" label="内容/皮纹代码">
                      </el-table-column>
                      <el-table-column prop="addUserName" label="经办">
                      </el-table-column>
                      <el-table-column prop="createTime" label="下单时间">
                      </el-table-column>
                      <el-table-column prop="finishTime" label="完成日期">
                      </el-table-column>
                      <el-table-column prop="gongXu" label="贴花工序">
                      </el-table-column>
                      <el-table-column prop="jingFeng" label="精封面积">
                      </el-table-column>
                      <el-table-column prop="tieHua" sortable label="每道面积">
                      </el-table-column>
                      <el-table-column prop="heJi" sortable label="合计">
                      </el-table-column>
                      <el-table-column prop="nanDu" sortable label="难度">
                      </el-table-column>
                    </el-table>
                  </div>
                </el-col>
              </el-row>
            </div>
          </el-card>
        </div>
        <div>
          <el-card class="box-card" style="margin-top:25px;">
            <div slot="header" class="clearfix">
              <span>汇总表</span>
            </div>
            <div>
              <div style="min-height:15px;width:100%;">
                <el-table :data="tableData6" border show-summary style="width: 100%">
                  <el-table-column prop="projectName" label="姓名">
                  </el-table-column>
                  <el-table-column prop="piWenDaiMa" label="工时">
                  </el-table-column>
                  <el-table-column prop="piWenDaiMa" label="上月遗留">
                  </el-table-column>
                  <el-table-column prop="addUserName" label="本月接单面积">
                  </el-table-column>
                  <el-table-column prop="createTime" label="总接单面积">
                  </el-table-column>
                  <el-table-column prop="finishTime" label="完成面积">
                  </el-table-column>
                  <el-table-column prop="gongXu" label="平均效率">
                  </el-table-column>
                </el-table>
              </div>
            </div>
          </el-card>
        </div>
      </el-main>
    </el-container>
  </div>
</div>
</template>
<script>
export default {
  data() {
    return {
      tableData6: [{
        projectName: '12987122',
        piWenDaiMa: '王小虎',
        addUserName: '234',
        createTime: '3.2',
        finishTime: 10,
        gongXu: 2,
        jingFeng: 45,
        tieHua: 450,
        heJi: 400,
        nanDu: 1
      }, {
        projectName: '12987122',
        piWenDaiMa: '王小虎',
        addUserName: '234',
        createTime: '3.2',
        finishTime: 10,
        gongXu: 2,
        jingFeng: 45,
        tieHua: 450,
        heJi: 400,
        nanDu: 1
      }, {
        projectName: '12987122',
        piWenDaiMa: '王小虎',
        addUserName: '234',
        createTime: '3.2',
        finishTime: 10,
        gongXu: 2,
        jingFeng: 45,
        tieHua: 450,
        heJi: 400,
        nanDu: 1
      }, {
        projectName: '12987122',
        piWenDaiMa: '王小虎',
        addUserName: '234',
        createTime: '3.2',
        finishTime: 10,
        gongXu: 2,
        jingFeng: 45,
        tieHua: 450,
        heJi: 400,
        nanDu: 1
      }, {
        projectName: '12987122',
        piWenDaiMa: '王小虎',
        addUserName: '234',
        createTime: '3.2',
        finishTime: 10,
        gongXu: 2,
        jingFeng: 45,
        tieHua: 450,
        heJi: 400,
        nanDu: 1
      }]
    }
  },
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val);
    }
  },
  methods: {
    getSummaries(param) {
      const {
        columns,
        data
      } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '总价';
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          sums[index] += ' 元';
        } else {
          sums[index] = 'N/A';
        }
      });

      return sums;
    },
    getMouldTree() {
      let self = this
      this.getRequest("/order/list/mould/tree").then(resp => {
        this.tree_loading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.data3 = resp.data.data.tree;
        }
      })
    }
  },
  mounted: function() {
    this.$nextTick(function() {})

    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');
    // this.getMouldTree();
  }
}
</script>
<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.el-select .el-input {
  width: 130px;
}

.card-bottom {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: right;
}

.input-with-select .el-input-group__prepend {
  background-color: #fff;
}

.card-bottom-left {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: left;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.mould-card {
  margin-bottom: 15px;
}

.mould-card:hover {
  transform: translateY(-2%);
  box-shadow: 1px 4px 10px 2px #CCC;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.pre-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
}
</style>



